.technics {
  margin-top: 50px;
  position: relative; }
  @media (max-width: 767px) {
    .technics {
      margin-top: 24px; } }
  .technics__container {
    overflow: hidden;
    margin-top: 24px;
    padding: 0 30px 32px 30px; }
    @media (max-width: 1365px) {
      .technics__container {
        padding: 0 24px 32px 24px; } }
    @media (max-width: 540px) {
      .technics__container {
        padding: 0 16px 32px 16px; } }
  .technics__title {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-transform: uppercase;
    color: #7e7e82; }
  .technics__row {
    display: grid;
    justify-content: space-between;
    grid-template-columns: repeat(auto-fit, 240px);
    grid-template-rows: 1fr;
    grid-row-gap: 16px;
    grid-column-gap: 8px; }
    @media (max-width: 1365px) {
      .technics__row {
        grid-template-columns: repeat(auto-fit, 240px); } }
    @media (max-width: 1100px) {
      .technics__row {
        display: flex;
        grid-column-gap: unset; } }
  .technics__show-more {
    margin-top: 24px;
    cursor: pointer; }
    @media (max-width: 1100px) {
      .technics__show-more {
        display: none; } }
