.services {
  margin-top: 42px; }
  .services__swiper {
    overflow: hidden; }
  .services__container {
    padding: 0 30px 0 30px; }
    @media (max-width: 1365px) {
      .services__container {
        padding: 0 24px 0 24px; } }
    @media (max-width: 540px) {
      .services__container {
        padding: 0 16px 0 16px; } }
  .services__row {
    display: flex; }
  .services__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 40px;
    letter-spacing: -0.6px;
    color: #1b1c21;
    flex: 1 1 100%;
    position: relative; }
    .services__title::before {
      content: '';
      position: absolute;
      height: 40px;
      width: 3px;
      border-radius: 2px;
      top: 0;
      left: -30px;
      background-color: #7e7e82; }
      @media (max-width: 1365px) {
        .services__title::before {
          left: -24px; } }
  .services__button-box:not(:last-child) {
    margin-right: 32px; }
  .services__long-line {
    margin-top: 32px; }
  .services__about {
    margin-top: 32px; }
