body {
  padding: 0;
  margin: 0;
  font-family: TTLakes, sans-serif;
  background-color: #f8f8f8;
  color: #1b1c21;
  position: relative; }

.container {
  padding: 0 30px;
  max-width: 1382px; }
  @media (max-width: 1365px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 540px) {
    .container {
      padding: 0 16px; } }
  .container--main {
    margin: 0 auto;
    padding: 0 0 0 0; }
    @media (max-width: 1365px) {
      .container--main {
        padding: 0 0 0 0; } }
    @media (max-width: 540px) {
      .container--main {
        padding: 0 0 0 0; } }

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 320px minmax(1000px, 1100px);
  background-color: #f8f8f8;
  overflow: hidden; }
  .wrapper--main {
    display: block; }
  @media (max-width: 1365px) {
    .wrapper {
      display: flex;
      flex-direction: column;
      position: relative; }
      .wrapper--main {
        display: block; } }

.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 0;
  left: 0;
  width: 100%; }

.swiper-pagination-bullet-active {
  background: #B5B6BC;
  opacity: 1; }
