.about__row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 36px;
  align-items: flex-start; }
  @media (max-width: 767px) {
    .about__row {
      grid-template-columns: repeat(1, 1fr); } }

.about__col img {
  width: 100%; }

.about__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px; }
  .about__text p:not(:last-child) {
    margin-bottom: 16px; }

.about__btn {
  margin-top: 15px; }
