.modal {
  display: grid;
  position: absolute;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 2000;
  grid-template-columns: 2fr 1fr;
  transform: translateX(107%);
  transition: all .3s; }
  .modal--active {
    transform: translateX(0); }
  @media (max-width: 1100px) {
    .modal {
      grid-template-columns: 1fr 1fr; } }
  @media (max-width: 540px) {
    .modal {
      grid-template-columns: 1fr; } }
  .modal__bg {
    background-color: #ffffff;
    opacity: 0.9; }
    @media (max-width: 540px) {
      .modal__bg {
        display: none; } }
  .modal__body {
    background-color: #ffffff;
    position: relative;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }
  .modal__container {
    display: flex;
    flex-direction: column;
    height: 100vh; }
  .modal__title {
    position: relative;
    margin-top: 40px;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px; }
    .modal__title::before {
      content: '';
      position: absolute;
      height: 40px;
      width: 3px;
      border-radius: 2px;
      top: 50%;
      transform: translateY(-50%);
      left: -30px;
      background-color: #7e7e82; }
      @media (max-width: 1365px) {
        .modal__title::before {
          left: -24px; } }
      @media (max-width: 540px) {
        .modal__title::before {
          left: -16px; } }
  .modal__text {
    margin-top: 16px;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #7e7e82; }
    .modal__text span {
      color: #ff3e79; }
  .modal__close {
    position: absolute;
    left: -64px;
    top: 32px; }
    @media (max-width: 540px) {
      .modal__close {
        position: static;
        left: 0;
        top: 0;
        margin-left: 16px;
        margin-top: 24px; } }
  .modal__input {
    margin-top: 24px;
    background: #ffffff;
    border: 1px solid #EAEAEA;
    border-radius: 8px;
    padding: 12px 16px; }
    .modal__input:not(:first-child) {
      margin-top: 16px; }
  .modal textarea:focus, .modal input:focus {
    outline: none; }
  .modal__textarea {
    margin-top: 16px;
    background: #ffffff;
    border: 1px solid #EAEAEA;
    border-radius: 8px;
    resize: none;
    padding: 12px 16px; }
    .modal__textarea:focus {
      border: none; }
  .modal__btn {
    position: relative;
    align-self: flex-end;
    margin-top: 24px;
    min-width: 136px;
    font-weight: 700;
    font-size: 12px;
    line-height: 24px;
    text-align: right;
    letter-spacing: -0.15px;
    text-transform: uppercase;
    color: #ffffff;
    background: #ff3e79;
    border-radius: 16px;
    border: none;
    padding: 4px 38px 4px 8px; }
    .modal__btn:after {
      content: '';
      position: absolute;
      width: 8px;
      height: 10px;
      background-image: url(./static/arrow-right-white.svg);
      background-repeat: no-repeat;
      top: 50%;
      right: 15px;
      transform: translateY(-50%); }
