.button {
  border: none;
  background-color: #ff3e79;
  color: #ffffff;
  cursor: pointer; }
  .button__link {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }

.button--oval {
  position: relative;
  background: #FF3E79;
  border-radius: 16px;
  font-weight: 700;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  padding: 4px 40px 4px 12px;
  transition: all 0.3s;
  border: 2px solid transparent; }
  .button--oval:before {
    content: '';
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    background-image: url(./static/arrow-right-white.svg);
    background-repeat: no-repeat;
    width: 10px;
    height: 10px; }
  .button--oval:hover {
    background-color: #eaeaea;
    color: #ff3e79;
    border: 2px solid #ff3e79; }

.button--circle {
  min-height: 40px;
  min-width: 40px;
  max-height: 40px;
  max-width: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s; }
  .button--circle:hover {
    background-color: #7e7e82; }
    .button--circle:hover svg {
      color: #ff3e79; }

.button--circle-outlined {
  background-color: transparent;
  border: 2px solid #ff3e79; }
  .button--circle-outlined:hover {
    border: 2px solid #ffe0e9;
    background-color: #ffe0e9; }

.button--circle img {
  object-fit: cover;
  max-width: 20px; }

.button--with-arrow {
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  background-color: transparent;
  border: none;
  padding-left: 32px;
  transition: color .3s; }
  .button--with-arrow:hover {
    color: #ff3e79; }
  .button--with-arrow:before {
    content: '';
    position: absolute;
    background-image: url(./static/arrow-down.svg);
    background-repeat: no-repeat;
    height: 24px;
    width: 24px;
    top: 0;
    left: 0; }
