.page-divider {
  width: 100%;
  height: 56px;
  background: linear-gradient(90deg, #F8F8F8 0%, #FFFFFF 98.61%);
  display: flex;
  align-items: center;
  padding-left: 32px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  color: #7e7e82; }
  @media (max-width: 1365px) {
    .page-divider {
      padding-left: 24px; } }
  @media (max-width: 540px) {
    .page-divider {
      padding-left: 16px; } }
