.footer {
  margin-top: 50px;
  padding-bottom: 25px; }
  .footer__container {
    display: flex;
    padding-top: 32px;
    justify-content: space-between;
    width: 100%;
    gap: 16px;
    border-top: 3px solid #d9fff5; }
    @media (max-width: 767px) {
      .footer__container {
        flex-direction: column;
        align-items: center; } }
    .footer__container p {
      max-width: 320px;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      color: #7e7e82;
      mix-blend-mode: normal;
      opacity: 0.72; }
      @media (max-width: 767px) {
        .footer__container p {
          text-align: center; } }
    .footer__container p:first-child {
      text-align: left; }
      @media (max-width: 767px) {
        .footer__container p:first-child {
          text-align: center; } }
    .footer__container p:last-child {
      text-align: right; }
      @media (max-width: 767px) {
        .footer__container p:last-child {
          text-align: center; } }
    .footer__container p:nth-child(2) {
      text-align: center;
      white-space: nowrap; }
