.menu {
  display: flex;
  flex-direction: column;
  gap: 32px; }
  @media (max-height: 767px) {
    .menu {
      gap: 24px; } }
  .menu__link {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1b1c21;
    text-decoration: none;
    font-weight: 500;
    position: relative; }
    .menu__link:hover {
      color: #ff3e79; }
      .menu__link:hover:before {
        content: '';
        position: absolute;
        height: 32px;
        width: 4px;
        background-color: #7e7e82;
        top: 50%;
        left: -30px;
        transform: translateY(-50%);
        border-radius: 2px; }
        @media (max-width: 1365px) {
          .menu__link:hover:before {
            left: -24px; } }
        @media (max-width: 540px) {
          .menu__link:hover:before {
            left: -16px; } }
    .menu__link--active {
      position: relative;
      color: #7e7e82; }
      .menu__link--active:before {
        content: '';
        position: absolute;
        height: 32px;
        width: 4px;
        background-color: #7e7e82;
        top: 50%;
        left: -30px;
        transform: translateY(-50%);
        border-radius: 2px; }
    @media (max-width: 1365px) {
      .menu__link--active:before {
        left: -24px; } }
    @media (max-width: 540px) {
      .menu__link--active:before {
        left: -16px; } }
