.header {
  background-color: #ffffff;
  max-height: 88px;
  display: none; }
  @media (max-width: 1365px) {
    .header {
      display: block;
      flex: 1 0 auto; } }
  .header__container {
    display: flex;
    align-items: center;
    padding-top: 18px;
    padding-bottom: 18px; }
  .header__center {
    display: flex;
    align-items: center;
    flex: 1 1 100%;
    gap: 16px; }
    @media (max-width: 550px) {
      .header__center button {
        display: none; } }
  .header__right {
    display: flex;
    align-items: center;
    gap: 16px; }
  .header__logo {
    width: 100%; }
    @media (max-width: 540px) {
      .header__logo {
        display: flex;
        align-items: center;
        justify-content: flex-end; } }
  @media (max-width: 540px) {
    .header__divider-left {
      display: none; } }
