.prices {
  margin-top: 50px; }
  @media (max-width: 767px) {
    .prices {
      margin-top: 24px; } }
  .prices__container {
    overflow: hidden;
    position: relative; }
    @media (max-width: 767px) {
      .prices__container {
        background: #f8f8f8; } }
  .prices__table {
    background: #FFFFFF;
    border-radius: 6px; }
    @media (max-width: 767px) {
      .prices__table {
        margin-bottom: 32px; } }
    .prices__table--first {
      background: transparent;
      border-radius: 0;
      margin-top: 30px; }
      @media (max-width: 767px) {
        .prices__table--first {
          margin-top: 0; } }
  .prices__text {
    margin-top: 32px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    max-width: 420px; }
  .prices__btn {
    text-align: left;
    margin-top: 16px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    border: none;
    background-color: transparent;
    padding: 0;
    position: relative;
    cursor: pointer; }
    .prices__btn:after {
      content: '';
      position: absolute;
      top: 55%;
      right: -15px;
      transform: translateY(-50%);
      background-image: url(./static/arrow-right.svg);
      background-repeat: no-repeat;
      background-size: 100%;
      height: 10px;
      width: 6px;
      text-align: center; }
      @media (max-width: 323px) {
        .prices__btn:after {
          transform: none;
          top: 32px;
          bottom: 0;
          right: 0;
          left: 110px; } }
