.long-line {
  display: flex; }
  @media (max-width: 1100px) {
    .long-line {
      flex-wrap: nowrap;
      gap: 0; } }
  .long-line__item {
    flex: 1 1 auto; }
    @media (max-width: 767px) {
      .long-line__item {
        width: 100%; } }
  .long-line__btn {
    background-color: transparent;
    padding: 8px 21px;
    white-space: nowrap;
    cursor: pointer;
    transition: all .2s;
    display: inline-flex;
    border: 2px solid transparent;
    border-radius: 6px; }
    .long-line__btn:hover {
      color: #ff3e79;
      background-color: #ffffff;
      border: 2px solid #ffe0e9; }
    .long-line__btn--active {
      background: #FFFFFF;
      border: 2px solid #B8FFEC;
      border-radius: 6px; }
