.table {
  display: grid;
  background: #fff; }
  @media (max-width: 767px) {
    .table__title {
      grid-column: 1 / 3; } }
  @media (max-width: 767px) {
    .table {
      display: flex; } }
  .table__item:not(:last-child) {
    border-bottom: 1px solid #eaeaea; }
  @media (max-width: 767px) {
    .table__time {
      transform: translateY(-8px); } }
  .table__item {
    display: grid;
    grid-template-columns: 3fr 1.5fr 1.5fr 1fr;
    align-items: center;
    margin: 0 24px;
    padding: 23px 0;
    width: auto;
    gap: 8px; }
    @media (max-width: 767px) {
      .table__item {
        grid-template-columns: 1fr 1fr;
        background: #ffffff;
        margin: 0;
        padding: 16px 16px 10px;
        border-radius: 6px; } }
    .table__item p {
      display: none;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      color: #7e7e82;
      mix-blend-mode: normal;
      opacity: 0.72; }
      @media (max-width: 767px) {
        .table__item p {
          display: block;
          grid-column: 1 / 3; } }
    .table__item--description {
      border: none;
      padding: 16px 0;
      background: #f8f8f8;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      color: #7e7e82;
      mix-blend-mode: normal;
      opacity: 0.72; }
      @media (max-width: 767px) {
        .table__item--description {
          display: none; } }
  .table__button {
    justify-self: flex-end; }
    @media (max-width: 767px) {
      .table__button {
        grid-column: 2 / 3;
        transform: translateY(-8px); } }
