.aside {
  background-color: #ffffff;
  max-width: 320px;
  min-height: 100vh;
  z-index: 1000; }
  @media (max-width: 1365px) {
    .aside {
      min-width: 33vw; } }
  @media (max-width: 850px) {
    .aside {
      min-width: 50vw; } }
  @media (max-width: 550px) {
    .aside {
      min-width: 100vw; } }
  .aside__bg {
    z-index: 900;
    position: absolute;
    display: none;
    width: 100vw;
    height: 100vh;
    background: #ffffff;
    mix-blend-mode: normal;
    opacity: 0.9; }
    .aside__bg--active {
      display: block; }
  @media (max-width: 1365px) {
    .aside {
      transform: translateX(-100%);
      position: absolute;
      transition: all .3s; }
      .aside--active {
        transform: translateX(0);
        box-shadow: 16px 0 52px rgba(14, 24, 80, 0.2); } }
  .aside__top {
    display: flex;
    align-items: center;
    padding-top: 18px;
    padding-bottom: 18px; }
  .aside__logo {
    width: 100%;
    margin-left: 16px; }
    @media (max-width: 540px) {
      .aside__logo {
        display: flex;
        align-items: center;
        justify-content: flex-start; } }
  .aside__container {
    display: flex;
    flex-direction: column;
    min-height: 890px;
    z-index: 1000;
    position: relative; }
    @media (max-height: 1365px) {
      .aside__container {
        min-height: 100vh; } }
  .aside__body {
    flex: 1 1 100%;
    margin-bottom: 32px; }
  .aside__menu {
    margin-top: 46px; }
    @media (max-height: 767px) {
      .aside__menu {
        margin-top: 22px; } }
  .aside__languages {
    margin-bottom: 32px;
    display: flex;
    gap: 7px; }
  .aside__language {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    font-weight: 500;
    color: #1b1c21;
    background-color: transparent;
    border: none;
    padding: 0;
    margin: 0; }
    .aside__language--active {
      color: #7e7e82; }
  .aside__phone {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    color: #1b1c21; }
    @media (max-height: 767px) {
      .aside__phone {
        margin-bottom: 8px; } }
  .aside__mail {
    margin-bottom: 12px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1b1c21; }
  .aside__buttons {
    margin-bottom: 16px;
    display: flex;
    gap: 16px; }
  .aside__close {
    display: none; }
    @media (max-width: 1365px) {
      .aside__close {
        display: block; } }
